

































// Decorators
import { Component, Prop, Vue } from 'vue-property-decorator'

// Vuex
import { statePlayers, stateSettings } from '@/store/index'

// Components
import NameInput from './NameInput.vue'
import NumSelector from './NumSelector.vue'
import QSlider from 'quasar/src/components/slider/QSlider.js';

@Component({
  components: {
    'name-input': NameInput,
    'num-selector': NumSelector,
  }
})
export default class PlayerCard extends Vue {

  @Prop(String) private playerId!: string

  $refs!: { 'slider': QSlider }

  statePlayers = statePlayers
  stateSettings = stateSettings

  updateScore(val: string) {
    this.statePlayers.action_setPlayerScore({ id: this.playerId, score: parseInt(val) })
  }
}
